#data__me__containter {
	
	position: relative;
	overflow: hidden;
}
/*! added for better smoothness of movement   */
.data__me__element {
	transition: transform 0.1s;
}

/*! 

Styles below aren't part of plugin,
they are just for styling and better visualization
how everything work

  */

.main__container {
	height: 600px;
	width: 100%;
	background-color: blue;
}

.red__box {
	position: absolute;
	background-color: red;
	top: 50%;
	left: 50%;
	width: 50px;
	height: 50px;
}


.red__box:nth-child(2) {
	top: 80%;
	left: 30%;
}

.class__mobile {
	animation: my__Animation ease-in 20s infinite;
}

@keyframes my__Animation {
	0%{
		transform: translate(0px,0px);
	}

	10%{
		transform: translate(5px,10px);
	}

	20%{
		transform: translate(10px,-15px);
	}

	30%{
		transform: translate(15px,14px);
	}

	40%{
		transform: translate(7px,7px);
	}

	50%{
		transform: translate(0px,0px);
	}

	60%{
		transform: translate(-7px,-7px);
	}

	70%{
		transform: translate(-12px,-17px);
	}

	80%{
		transform: translate(-15px,-7px);
	}

	90%{
		transform: translate(-7px,-7px);;
	}

	100%{
		transform: translate(0px,0px);
	}
}